.modal-delete {
  position: fixed;
  width: 100%;
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.65);
  z-index: 99;
  top: -500%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  transition: all 1s;
}
.modal-delete .modal-delete_title {
  color: #fff;
  font-size: 24px;
  text-align: center;
}
.btns {
  display: flex;
  justify-content: center;
  align-items: center;
  column-gap: 10px;
  margin-top: 20px;
}
.btn {
  font-family: var(--rubik);
  font-size: 18px;
  font-weight: 500;
  width: 100px;
  border-radius: 10px;
  height: 40px;
  width: 120px;
  display: flex;
  align-items: center;
  justify-content: center;
  /* padding: 10px 20px; */
  color: #fff;
  cursor: pointer;
  transition: all 0.2s;
}
.btn:hover {
  box-shadow: 0 0 10px rgba(253, 253, 253, 1);
}
.delete {
  background-color: var(--color_danger);
}
.cancel {
  background-color: rgb(4, 156, 4);
}
